import { Component, ViewChild } from '@angular/core';

import { AlertController, App, ItemSliding, List, ModalController, NavController, ToastController, LoadingController} from 'ionic-angular';

import { UserData } from '../../providers/user-data';
import { ConferenceData } from '../../providers/conference-data';

import { SessionDetailPage } from '../session-detail/session-detail';
import { SymposiumDetailPage } from '../symposium-detail/symposium-detail';
import { SearchPage } from '../search/search';

@Component({
  selector: 'page-schedule',
  templateUrl: 'schedule.html'
})

export class SchedulePage {
  // the list is a child of the schedule page
  // @ViewChild('scheduleList') gets a reference to the list
  // with the variable #scheduleList, `read: List` tells it to return
  // the List and not a reference to the element
  @ViewChild('scheduleList', { read: List }) scheduleList: List;

  dayIndex = '0'
  queryText = '';
  segment = 'all';
  shownSessions: any = [];
  groups: any = [];
  confDate: string;

  constructor(
    public alertCtrl: AlertController,
    public app: App,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public navCtrl: NavController,
    public toastCtrl: ToastController,
    public confData: ConferenceData,
    public user: UserData
  ) {}

  ionViewDidLoad() {
    this.app.setTitle('Schedule');
    this.updateSchedule();
  }

  updateSchedule() {
    // Close any open sliding items when the schedule updates
    this.scheduleList && this.scheduleList.closeSlidingItems();

    this.confData.getTimeline(Number(this.dayIndex), this.queryText, this.segment).subscribe((data: any) => {
      this.shownSessions = data.shownSessions;
      this.groups = data.groups;
    });
  }

  showSearch() {
    this.navCtrl.push(SearchPage);
  }

  goToSessionDetail(sessionData: any) {
    // go to the session detail page
    // and pass in the session data

    if (sessionData.isSymposium) {
      this.navCtrl.push(SymposiumDetailPage, {sessionId: sessionData.id});
    } else {
      this.navCtrl.push(SessionDetailPage, { sessionId: sessionData.id, name: sessionData.name });
    }
  }

  addFavorite(slidingItem: ItemSliding, sessionData: any) {
    if (this.user.hasFavorite(sessionData.id)) {
      this.removeFavorite(slidingItem, sessionData, 'Favorite already added');
    } else {
      // remember this session as a user favorite
      this.user.addFavorite(sessionData.id);

      // create an alert instance
      let alert = this.alertCtrl.create({
        title: 'Favorite Added',
        buttons: [{
          text: 'OK',
          handler: () => {
            // close the sliding item
            slidingItem.close();
          }
        }]
      });
      // now present the alert on top of all other content
      alert.present();
    }

    /*this.user.hasFavorite(sessionData.name).then((res) => {
      if (res) {
        this.removeFavorite(slidingItem, sessionData, 'Favorite already added');
      } else {
        // remember this session as a user favorite
        this.user.addFavorite(sessionData.name);

        // create an alert instance
        let alert = this.alertCtrl.create({
          title: 'Favorite Added',
          buttons: [{
            text: 'OK',
            handler: () => {
              // close the sliding item
              slidingItem.close();
            }
          }]
        });
        // now present the alert on top of all other content
        alert.present();
      }
    })*/
  }

  removeFavorite(slidingItem: ItemSliding, sessionData: any, title: string) {
    let alert = this.alertCtrl.create({
      title: title,
      message: 'Would you like to remove this session from your favorites?',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            // they clicked the cancel button, do not remove the session
            // close the sliding item and hide the option buttons
            slidingItem.close();
          }
        },
        {
          text: 'Remove',
          handler: () => {
            // they want to remove this session from their favorites
            this.user.removeFavorite(sessionData.id);
            this.updateSchedule();

            // close the sliding item and hide the option buttons
            slidingItem.close();
          }
        }
      ]
    });
    // now present the alert on top of all other content
    alert.present();
  }
}



