import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

const KEY = 'favorites';

@Injectable()
export class UserData {

favorites = []

  constructor(public storage: Storage) {
    this.getFavorites().then((res) => {
      if (res) {
        this.favorites = res;
      }
    });
  }

  hasFavorite(sessionID: number): boolean {
    return this.favorites.indexOf(sessionID) !== -1;
  };

  addFavorite(sessionID: number) {
    this.favorites.push(sessionID);
    this.storage.set(KEY, this.favorites);
  };

  removeFavorite(sessionID: number) {
    var index = this.favorites.indexOf(sessionID);
    this.favorites.splice(index, 1);
    this.storage.set(KEY, this.favorites);
  };

  getFavorites() {
    return this.storage.get(KEY);
  }
}



